<template>
  <div class="login-box-div">
    <div class="juzhong">
      <el-form
        :model="forgetpassInfo"
        :rules="rules"
        ref="forgetpassForm"
        class="demo-ruleForm"
      >
        <div class="login-box-img">
          <img src="../../assets/img/更改密码.png" alt="" />
        </div>
        <el-form-item prop="password">
          <div class="login-box-input">
            <el-input
              type="password"
              placeholder="NewPassword"
              v-model="forgetpassInfo.password"
            >
            </el-input>
          </div>
        </el-form-item>
        <el-form-item prop="email">
          <div class="login-box-input">
            <el-input placeholder="Email" v-model="forgetpassInfo.email">
            </el-input>
            <el-button
              class="btnsend"
              :disabled="btnDisable"
              @click="send"
              type="info"
              >{{ btnMsg }}</el-button
            >
            <!-- <el-button v-if="sendMsgDisabled" type="info">{{ time+'秒后获取'}}</el-button> -->
          </div>
        </el-form-item>
        <el-form-item prop="captcha">
          <div class="login-box-input">
            <el-input
              placeholder="验证码"
              v-model="forgetpassInfo.captcha"
            ></el-input>
          </div>
        </el-form-item>
        <div class="login-box-span">
          <span class="register-btn" @click="gotoLogin">返回登入</span>
        </div>
        <div class="login-box-button">
          <el-button class="login-button" type="info" @click="submitBtn"
            >更 改</el-button
          >
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    var checkemail = async (rule, value, callback) => {
      if (value != "") {
        const result = await this.checkEmail();
        if (!result) {
          callback(new Error("该邮箱还未被注册，请重新输入！"));
        } else {
          callback();
        }
      }
      callback();
    };
    return {
      rules: {
        password: [
          { required: true, message: "请输入新密码", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "长度在 6 到 20 个字符",
            trigger: ["blur", "change"],
          },
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          {
            required: true,
            type: "email",
            message: "请输入正确的邮箱",
            trigger: ["blur", "change"],
          },
          { validator: checkemail, trigger: "blur" },
        ],
      },
      forgetpassInfo: {
        password: "",
        email: "",
        captcha: "",
      },
      btnMsg: "获取验证码",
      btnDisable: false,
      time: 59,
    };
  },
  methods: {
    gotoLogin() {
      this.$router.push("/login");
    },
    async checkEmail() {
      // 如果邮箱存在返回true，不存在返回false
      const res = await this.$api.user.checkEmail({
        email: this.forgetpassInfo.email,
      });
      if (res.data.code === 200) {
        return false;
      } else {
        return true;
      }
    },
    async send() {
      this.$refs.forgetpassForm.validate(async (valid) => {
        if (valid) {
          const res = await this.$api.user.sendEmail({
            email: this.forgetpassInfo.email,
          });
          if (res.data.code === 200) {
            this.$message.success(res.data.msg);
            this.btnDisable = true;
            let i = 59;
            let taskId = setInterval(() => {
              this.btnMsg = i-- + "秒后重试";
              if (i === 0) {
                this.btnDisable = false;
                this.btnMsg = "获取验证码";
                clearInterval(taskId);
              }
            }, 1000);
          } else {
            this.$message.error(res.data.msg);
            this.btnDisable = true;
            let i = 59;
            let taskId = setInterval(() => {
              this.btnMsg = i-- + "秒后重试";
              if (i === 0) {
                this.btnDisable = false;
                this.btnMsg = "获取验证码";
                clearInterval(taskId);
              }
            }, 1000);
          }
        }
        return;
      });
    },
    submitBtn() {
      this.$refs.forgetpassForm.validate(async (valid) => {
        if (valid) {
          if (
            this.forgetpassInfo.captcha === null ||
            this.forgetpassInfo.captcha === ""
          ) {
            this.$message.error("请输入验证码");
          } else {
            const res = await this.$api.user.forgetPassword(
              this.forgetpassInfo
            );
            if (res.data.code === 200) {
              this.$message.success(res.data.msg);
              this.$router.push("/login");
            } else {
              this.$message.error(res.data.msg);
            }
          }
        }
        return;
      });
    },
  },
};
</script>

<style scoped>
.btnsend {
  padding: 12px 15px;
}
.juzhong {
  width: 280px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
img {
  width: 85px;
  height: 85px;
}
.login-box-div {
  padding-top: 120px;
}
.login-box-img {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
.login-box-input {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 280px;
}
.login-box-span {
  margin: auto;
  display: flex;
  justify-content: space-between;
  width: 280px;
  color: #666666;
}
.login-box-button {
  margin: auto;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.login-button {
  width: 280px;
  background-color: #eeeeee;
  border: solid 1px #dcdfe6;
  color: #666666;
}
.login-button:hover {
  background-color: #e9b5fa;
  border: solid 1px #e9b5fa;
  color: white;
}
.register-btn {
  color: #0090f7;
  cursor: pointer;
}
.forget-password-btn {
  color: #0090f7;
  cursor: pointer;
}
</style>